<template>
  <div>
    <v-bottom-sheet v-model="sheet">
      <v-card height="200">
        <v-card-text>
          <v-btn variant="text" @click="sheet = !sheet"> X </v-btn>

          <br />
          <br />

          <div>{{ popContents }}</div>
        </v-card-text>
      </v-card>
    </v-bottom-sheet>
  </div>
  <div id="title"><img
          :src="require(`@/assets/jam-icon.png`)"
          width="50px"
          height="30px"
        />JAMMAP</div>

  <naver-map
    style="width: 100%; height: 500px"
    :map-options="mapOptions"
    @onLoad="onLoadMap($event)"
  >
    <naver-marker
      v-for="data in datas"
      :key="data.id"
      :latitude="data.lat"
      :longitude="data.lng"
      @onLoad="onLoadMarker($event)"
      @click="onClick(data.id)"
    >
      <div class="marker">
        <img
          :src="require(`@/assets/marker/${data.imageName}.png`)"
          width="60px"
          height="60px"
        />
      </div> </naver-marker
    >
    <!-- <naver-polyline :path="datas" /> -->
  </naver-map><div>Copyright © 2024 by <a href="https://twitter.com/theminjoo_bot" target="_blank">민주당홍보봇</a>. All Rights Reserved.</div>
  <div style="position:fixed;bottom:0px;right:0px;z-index:99999999" ><img
          :src="require(`@/assets/corner-icon.png`)"
          width="120px"
          height="110px"
        /></div>

        
  
</template>
<script>
import { ref } from "vue";
import { NaverMap } from "vue3-naver-maps";
import { NaverMarker } from "vue3-naver-maps";

import jamData from "@/assets/jamData.js";

export default {
  name: "App",
  components: {
    NaverMap,
    NaverMarker,
  },
  setup() {
    let sheet = ref(false);
    let popContents = ref("");
    const mapOptions = {
      latitude: 37.51347, // 지도 중앙 위도
      longitude: 127.041722, // 지도 중앙 경도
      zoom: 13,
    };
    const onLoadMarker = (marker) => {
      marker._icon.anchor = new naver.maps.Point(40, 40);
      marker.draw();
      //marker.setAnchor(new naver.maps.Point(0, 0)
      //); // Change Map Center
    };
    const onLoadMap = (map) => {
      console.log(map);
      //marker.setAnchor(new naver.maps.Point(0, 0)
      //); // Change Map Center
    };
    const onClick = (id) => {
      sheet.value = !sheet.value;
      console.log(sheet);
      popContents.value = id;
      //marker.setAnchor(new naver.maps.Point(0, 0)
      //); // Change Map Center
    };
    const datas = jamData;

    return {
      mapOptions,
      datas,
      onLoadMarker,
      onLoadMap,
      onClick,
      popContents,
      sheet,
    };
  },
};
</script>

<style>
#title{font-size:30px}
#app {font-family: "chab", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

@font-face {
  font-family:'chab';
  src: url('assets/fonts/chab.ttf') format('truetype');
   font-weight: normal;
  font-style: normal;
}@font-face {
  font-family: "DungGeunMo";
  src: url("assets/fonts/DungGeunMo.woff2") format("woff2"),
    url("assets/fonts/DungGeunMo.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
</style>
